.HomePage {
    h1 {
        text-transform: uppercase;
        color: var(--themered);
        &:first-child {
            margin-top: 0;
        }
        text-align: center;
    }

    p  {
        font-family: monospace;
        color: #1A0000;
        font-size: 1.5em;
        max-width: 600px;
        margin: 0 auto;
        text-align: center;
    }


    .name-background {
        user-select: none !important;
        height: max(100vh, 800px);
        background-color: var(--themered);
        position: relative;
        img {
            position: absolute;
                top: calc(50% - 2em);
                left: 35%;
                -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                max-width: 600px;
                width: 100%;
        }
        .name { 
            margin: 0;
            position: absolute;
            top: calc(50% - 2em);
            left: 68%;
            -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            // max-width: 1400px;
            // width: 100%;
            div {
                font-size: 15em;
                font-weight: 400;
                color: #1A0000;
                display: inline-block;
                // user-select: none;
                margin-top: -0.12em;
                margin-bottom: -0.12em;
                &.luke {
                    letter-spacing: 0.13em;
                }
                &.red {
                    letter-spacing: 0.5em;
                }
            }
        }
    
    }
    .moreinfo {
        padding: 3em;
        height: max(100vh, 800px);
        background-color: white;
        // text-align: center;
    }
}