.Layout {
    // max-width: 800px;
    
    margin: 0 auto;
    position: relative;
    nav {
        position: fixed;
        z-index: 3;
        width: 100vw;
        // background-color: green;
        background-color: var(--themered);
        display: flex;
        justify-content: space-between;
        .name {
            display: flex;
                height: 3em;
                align-items: center;
            color: #1A0000;
                text-decoration: none;
                font-weight: 700;
                margin-left: 1em;
        }
        .links {
            display: flex;
            flex-direction: row-reverse;
            // padding: 0 1em;
            height: 3em;
            align-items: center;
            user-select: none;
            a {
                color: #1A0000;
                text-decoration: none;
                font-weight: 700;
                margin-left: 1em;
                &:first-child { //actually last child but first bc row-reverse
                    margin-right: 1em;
                }
            }
        }
    }

    footer {
        background-color: var(--themered);
    }
}